import React, {useState} from "react"
import {DemoForm} from "components/common/DemoForm";
import {Button, Col, Row, Tabs} from "antd";
import "./Editor.css"
import MonacoEditor from "react-monaco-editor";
import {JsEditor} from "./JsEditor";
import {LeftOutlined, RightOutlined} from '@ant-design/icons';

const {TabPane} = Tabs;

const monacoEditorOptions = {
    minimap: {
        enabled: false,
    },
    automaticLayout: true,
};

const createEditor = ({editor, value, onChange, options, height}) => {
    return <div className="jeditor-panel">
        <MonacoEditor className="jeditor" language="json" value={JSON.stringify(value, null, 2)} theme="vs-light"
                      onChange={x => onChange(JSON.parse(x))}
                      height={height || 400}
                      options={{...monacoEditorOptions, ...options}}
                      {...editor}
        />
    </div>
}

export const Editor = (props) => {
    const {formProps, code, show = {schema: true, data: true, configSchema: true}, useTabs = true, height} = props;
    const [schema, changeSchema] = useState(props.schema)
    const [configSchema, changeConfigSchema] = useState(props.configSchema)
    const [data, changeData] = useState(props.data)
    const [showEditor, setShowEditor] = useState(true);

    const onChange = data => {
        if (props.onChange) {
            props.onChange();
        }
        changeData(data)
    }

    const editors = {
        schema: show.schema && schema && createEditor({height, value: schema, onChange: changeSchema}),
        configSchema: show.configSchema && schema && createEditor({
            height,
            value: configSchema,
            onChange: changeConfigSchema
        }),
        data: show.data && createEditor({value: data, height, onChange: onChange}),
        jsx: show.code && code && <JsEditor height={height} code={code}/>
    }

    const content = Object.entries(editors).filter(([k, v]) => v).map(([k, v], i) => (
            useTabs ? <TabPane key={i} tab={k}>
                {v}
            </TabPane> : v
        )
    )

    const form = <DemoForm {...formProps} height={height} schema={schema} configSchema={configSchema} data={data}
                           onChange={onChange}
                           schemaInitialized={({data}) => onChange(data)}/>


    return <div>
        <Row>
            {<Button className="editor-hide-button" onClick={() => setShowEditor(!showEditor)}
                     icon={showEditor ? <LeftOutlined/> : <RightOutlined/>}/>}
            {showEditor && <Col span={12}>
                {
                    useTabs ?
                        <Tabs tabPosition="top">
                            {content}
                        </Tabs>
                        : content
                }
            </Col>
            }
            <Col span={showEditor ? 12 : 24}>
                {
                    useTabs ?
                        <Tabs tabPosition="top">
                            <TabPane key={1} tab="Demo">
                                {form}
                            </TabPane>
                        </Tabs>
                        : form
                }
            </Col>
        </Row>
    </div>
}