import React from "react";
import {Solutions} from "../components/solutions/Solutions";
import {Navigate} from "react-router-dom";
import {Home} from "../components/home/Home";
import {
    COMPONENTS_PATH,
    DOCUMENTATION_CONFIG_SCHEMA_ELEMENTS_PATH,
    DOCUMENTATION_CONFIG_SCHEMA_LAYOUT_PATH,
    DOCUMENTATION_CONFIG_SCHEMA_PATH,
    DOCUMENTATION_CONFIG_SCHEMA_STYLES_PATH, DOCUMENTATION_DEFAULTS_PATH, DOCUMENTATION_EVENTS_PATH,
    DOCUMENTATION_PATH,
    DOCUMENTATION_SCHEMA_PATH,
    DOCUMENTATION_TYPES_BOOLEAN_PATH, DOCUMENTATION_TYPES_OBJECT_PATH,
    DOCUMENTATION_TYPES_PATH,
    DOCUMENTATION_TYPES_STRING_PATH,
    EXAMPLES_PATH,
    SOLUTIONS_PATH
} from "routes/constants";
import {BooleanTypeDocumentation} from "components/documentation/types/BooleanTypeDocumentation";

export default [
    {
        path: "",
        element: <Home/>
    },
    {
        path: DOCUMENTATION_PATH,
        children: [
            {path: "", element: <Navigate to={DOCUMENTATION_SCHEMA_PATH} replace/>},
            {path: DOCUMENTATION_SCHEMA_PATH, element: DOCUMENTATION_SCHEMA_PATH},
            {
                path: DOCUMENTATION_CONFIG_SCHEMA_PATH, children: [
                    {path: "", element: <Navigate to={DOCUMENTATION_CONFIG_SCHEMA_ELEMENTS_PATH} replace/>},
                    {path: DOCUMENTATION_CONFIG_SCHEMA_ELEMENTS_PATH, element: DOCUMENTATION_CONFIG_SCHEMA_ELEMENTS_PATH},
                    {path: DOCUMENTATION_CONFIG_SCHEMA_LAYOUT_PATH, element: DOCUMENTATION_CONFIG_SCHEMA_LAYOUT_PATH},
                    {path: DOCUMENTATION_CONFIG_SCHEMA_STYLES_PATH, element: DOCUMENTATION_CONFIG_SCHEMA_STYLES_PATH}
                ]
            },
            {
                path: DOCUMENTATION_TYPES_PATH, children: [
                    {path: "", element: <Navigate to={DOCUMENTATION_TYPES_STRING_PATH} replace/>},
                    {path: DOCUMENTATION_TYPES_STRING_PATH, element: DOCUMENTATION_TYPES_STRING_PATH},
                    {path: DOCUMENTATION_TYPES_BOOLEAN_PATH, element: <BooleanTypeDocumentation/>},
                    {path: DOCUMENTATION_TYPES_OBJECT_PATH, element: DOCUMENTATION_TYPES_OBJECT_PATH}
                ]
            },
            {path: DOCUMENTATION_EVENTS_PATH, element: DOCUMENTATION_EVENTS_PATH},
            {path: DOCUMENTATION_DEFAULTS_PATH, element: DOCUMENTATION_DEFAULTS_PATH}

        ],
    },
    {
        path: COMPONENTS_PATH
    },
    {
        path: EXAMPLES_PATH
    },
    {
        path: SOLUTIONS_PATH,
        children: [
            {path: "", element: <Navigate to="/solutions/0" replace/>},
            {path: ":id", element: <Solutions/>}
        ]
    }
]